import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './Global.module.css';

gsap.registerPlugin(ScrollTrigger);

function Global() {
  const path = process.env.PUBLIC_URL;
  const textRefs = useRef([]);
  useEffect(() => {
      textRefs.current.forEach((text, index) => {
        gsap.fromTo(
          text,
          { y: -50, opacity: 0 }, //  :    
          {
            y: 0, //  :  
            opacity: 1, //  
            duration: 1, //   
            ease: 'power3.out', //  
            scrollTrigger: {
              trigger: text, //   
              start: 'top 90%', //  90%   
              end: 'top 70%', // 70%   
              scrub: false, //      ( )
              once: true, //    
            },
          }
        );
      });
    }, []);

  return (
    <div className={styles.global}>
      <p ref={(el) => el && textRefs.current.push(el)}>HBAF 2015     <strong> 25</strong>    <strong>  </strong> </p>
      <img className={styles.map} src={path + "./images/global-img.png"} alt="" ref={(el) => el && textRefs.current.push(el)} />
    </div>
  );
}

export default Global;