import styles from './Footer.module.css';

function Footer() {
  const path = process.env.PUBLIC_URL; 
  return (
    <footer>
    <h2 class={styles.footerLogo}>
      <img src={path + "./images/logo.png"} alt="footer logo"/>
    </h2>
    <div class={styles.address}>
      <p> |    176 ()</p>
      <p> |     45 6, 3 ()</p>
      <p> |      39 ()</p>
      <p>hbaf@hbaf.co.kr</p>
    </div>
    <div class={styles.copyrights}>
      <p>Copyrights 2023 HBAF All Rights Reserved</p>
    </div>
    <div class={`${styles.footerBottom} flex`}>
      <div class={styles.footerLeft}>
        <h4>CATEGORY</h4>
        <p>ABOUT</p>
        <p>PRODUCTS</p>
        <p>FLAVORS</p>
        <p>STORY</p>
        <p>CONTACT</p>
      </div>
      <div class={styles.footerRight}>
        <h4>CS CENTER</h4>
        <h4>080-700-8383</h4>
        <p>9 -  5</p>
        <p> <a href="https://sihyun96.github.io/sihyun-portfolio/" target='blank'> </a>
        <a href="https://github.com/sihyun96/241216-hbaf.git" target='blank'>github </a></p>
      </div>
    </div>
  </footer>
  );
}

export default Footer;