import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './Value.module.css';

gsap.registerPlugin(ScrollTrigger);

const ValueSection = () => {
  const imgRefs = useRef([]);
  const rotateValues = [6, -4, -10, 10, -10, 10, 10, -20, 10]; //    

  useEffect(() => {
    imgRefs.current.forEach((img, index) => {
      const rotateValue = rotateValues[index] || 0; 

      gsap.fromTo(
        img,
        { rotate: 0 }, //  : 0
        {
          rotate: rotateValue, //      
          duration: 10, //   
          scrollTrigger: {
            trigger: img, //  
            start: 'top center', //      
            end: 'top top', //      
            scrub: 0.5, //    
          },
        }
      );
    });
  }, []);

  return (
    <div className={styles.value}>
      <div className={styles.wrapper1}>
        <div className={`${styles.valueImg} grid`}>
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img1-1.png"
            alt=" "
          />
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img1-2.png"
            alt=" "
          />
        </div>
        <div className={'num'}>1</div>
        <h3 className={'subTitle'}>GOOD FOR HEALTH</h3>
        <p>
                <strong>HBAF </strong>
        </p>
      </div>

      <div className={styles.wrapper2}>
        <div className={`${styles.valueImg} grid`}>
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img2-1.png"
            alt=" "
          />
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img2-2.png"
            alt=" "
          />
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img2-3.png"
            alt=" "
          />
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img2-4.png"
            alt=" "
          />
        </div>
        <div className={'num'}>2</div>
        <h3 className={'subTitle'}>Craving New Flavors</h3>
        <p>    <strong>HBAF </strong></p>
      </div>

      <div className={styles.wrapper3}>
        <div className={`${styles.valueImg}`}>
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img3-1.png"
            alt=" "
          />
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img3-2.png"
            alt=" "
          />
          <img
            ref={(el) => el && imgRefs.current.push(el)}
            src="./images/value-img3-3.png"
            alt=" "
          />
        </div>
        <div className={'num'}>3</div>
        <h3 className={'subTitle'}>Diversity</h3>
        <p>
               {' '}
          <strong>HBAF </strong>
        </p>
      </div>
    </div>
  );
};

export default ValueSection;
