import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './MainTxt.module.css';

gsap.registerPlugin(ScrollTrigger);

function MainTxt() {
  const textRefs = useRef([]); //  h3  

  useEffect(() => {
    textRefs.current.forEach((text) => {
      gsap.fromTo(
        text,
        { y: -50, opacity: 0 }, //  :    
        {
          y: 0, //  :  
          opacity: 1, //  
          duration: 1, //   
          ease: 'power3.out', //  
          scrollTrigger: {
            trigger: text, //   
            start: 'top 90%', //  90%   
            end: 'top 70%', // 70%   
            scrub: true, //  
            once: false, //     
            toggleActions: 'play none none reverse', //    /
          },
        }
      );
    });
  }, []);

  return (
    <>
      <h1 className={styles.logo}>
        <img src="./images/logo.svg" alt="" />
      </h1>
      <div className={styles.mainTxtWrapper}>
        <h3
          className={styles.mainStroke}
          data-content="Koreas Largest"
          ref={(el) => el && textRefs.current.push(el)}
        >
          Koreas Largest
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="Nut Brand"
          ref={(el) => el && textRefs.current.push(el)}
        >
          Nut Brand
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="HBAP, H is silent"
          ref={(el) => el && textRefs.current.push(el)}
        >
          HBAP, H is silent
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="My best friend,"
          ref={(el) => el && textRefs.current.push(el)}
        >
          My best friend,
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="My HBAF"
          ref={(el) => el && textRefs.current.push(el)}
        >
          My HBAF
        </h3>
      </div>
    </>
  );
}

export default MainTxt;
